import React, { Component } from "react";
import { connect } from "react-redux";

import { getData } from "../../actions/index";
import Header from "../../componets/header/header";
import Footer from "../../componets/footer/footer";
import BaseSelect from "../../componets/select/select";
import UserCard from "../../componets/userCard/userCard";
import CssBaseline from "@material-ui/core/CssBaseline";
import "./userPage.css";

class UserPage extends Component {
  state = {
    userName: ""
  };

  componentDidMount() {
    this.props.getData();
  }

  renderSelectOptions = () => {
    return this.props.users.map(dt => (
      <option key={dt.id} value={dt.username}>
        {dt.username}
      </option>
    ));
  };

  handleChange = ({ target: { value } }) => {
    this.setState({
      userName: value
    });
    console.log(value);
  };

  render() {
    const { userName } = this.state;
    const { users, getData } = this.props;
    const renderSelectOptions = this.renderSelectOptions();

    return (
      <div className="userPage">
        <CssBaseline />
        <Header />
        <BaseSelect
              value={userName}
              getData={getData}
              renderSelectOptions={renderSelectOptions}
              handleChange={this.handleChange}
        />
        <UserCard />
        <Footer />
      </div>
    );
  }
}

export default connect(
  ({ users }) => ({
    users,
    
  }),
  { getData }
)(UserPage);
